@import url(https://fonts.googleapis.com/css?family=Open+Sans);
body {
    background-color: #f9f9f9;
    font-family: Helvetica, Arial, sans-serif;
}

h1, h2, h3, h3 span, h4, h4 span {
    font-size: 30px;
    font-weight: bold;
    text-align: center;
}
h1 span {
    color: rgb(127,127,127);
}
h2 {
    text-align: left;
}
h3, h3 span {
    font-size: 26px;
}
h3 {
    margin-bottom: 0 !important;
    text-transform: uppercase;
}
h4.sv_p_title, h4.sv_p_title span {
    font-size: 20px;
    font-weight: bold;
    text-align: center;
}
.separator {
    background-color: rgb(153,153,153);
    width: 107px;
    height: 2px;
    margin: 28px auto;
    display: block;
}
.button-link, .sv-button-link {
    border: 1px solid rgb(127,127,127) !important;
    border-radius: 0 !important;
    color: #212529;
    width: 231px;
    height: 58px;
    font-family: Verdana, Geneva, Tahoma, sans-serif !important;
    font-size: 13.5px !important;
    font-weight: bold !important;
    line-height: 1em;
    text-transform: uppercase;
    text-align: center;
    text-decoration: none;
    display: inline-block;
}
.button-link {
    padding-top: 20px;
}
.button-link:hover {
    color: #212529;
}

.panel-heading.card-header {
    background-color: transparent;
    border-bottom: none;
    padding-top: 1.5rem;
    padding-bottom: 0;
    position: relative;
}
.sv_main .sv_header__text {
    display: inline !important;
}
.panel-heading.card-header::after, h4::after {
    content: "";
    background-color: rgb(153,153,153);
    width: 107px;
    height: 2px;
    margin: 28px auto 50px;
    display: block;
}
h4::after {
    margin-bottom: 28px;
}
h4.sv_p_title::after {
    display: none;
}
.small {
    margin-bottom: 45px;
    font-size: 1em !important;
    text-align: center !important;
}

/* Progress bar styles */
.sv_main.sv_main .progress {
    width: 80% !important;
    margin-bottom: 50px !important;
}
.progress {
    border-radius: 0 !important;
}
.progress-bar {
    background-color: #5a90a7 !important;
}

/* Question styles */
.sv_row {
    margin-bottom: 15px;
}
.sv_p_container {
    background-color: #5a90a7;
    color: #fff;
    overflow: hidden;
}
/*.sv_row:nth-child(even) .sv_p_container {
    background-color: rgba(103,143,164,0.75);
}*/
.sv_p_container h4, .sv_p_container h4 span {
    padding-top: 1.5em;
    padding-bottom: 1em;
    padding-right: 1em;
    text-align: left !important;
}
.sv_row .sv_row {
    border-bottom: 1px dotted #fff;
    margin-bottom: 15px;
}
.sv_row .sv_row:last-child {

}
.sv_row .sv_row h5 {
    font-size: 17px;
}
.sv_row .small {
    margin: 10px 0;
    overflow: hidden;
}
.sv_row .btn-group .btn {
    background-color: #fff;
    color: #35373c;
    border-color: #fff;
    min-width: 10px !important;
    margin-right: 5px;
    border-radius: 0;
    display: inline-block;
}
.sv_row .btn-group .btn:hover, .sv_row .btn-group .btn.active {
    background-color: #35373c;
    color: #fff;
    border-color: #35373c;
}

/* Survey footer styles */
.panel-footer.card-footer {
    background-color: transparent;
    border-top: none;
    margin-top: 20px;
    margin-bottom: 35px;
    text-align: center;
}
.panel-footer.card-footer .sv-button-link {
    margin: 0 10px;
}
/* Results table */
table {
    background-color: #5a90a7;
    border-color: #fff;
    border-style: solid;
    color: #fff;
    margin-bottom: 45px;
}
table tbody tr:last-child td {
    margin-bottom: 40px;
}
table tbody th td {
    padding: 1.25em 1.25em 0.5em;
}
table tbody tr td {
    border-bottom: 1px solid rgba(255,255,255, 0.25);
    padding: 0.5em 1.25em 0.5em;
}
table tbody th td, table tbody tr td {
    width: 100%;
    display: block;
}
